html {
  font-size: 62.5%;
}

html,
body {
  width: 100%;
}

body {
  font-family: 'Raleway', sans-serif;
}

button:active,
button:focus {
  outline: none;
}

.page {
  height: 100vh;
}

.flex-same-width {
  flex-grow: 1;
  flex-basis: 0;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.menu-toggle,
.logo {
  color: white;
  font-size: 3.2rem;
}

.logo {
  font-weight: 700;
  display: inline-block;
  text-transform: uppercase;
  padding: 0 0 0 15px;
}

header {
  position: relative;
}

/* Button */

.menu-toggle {
  padding: .75em 15px;
  line-height: 1em;
  float: right;
}

.menu-toggle:after {
  clear: both;
}

.menu-toggle:hover,
.menu-toggle:focus {
  outline: none;
}

/* Offscreen menu */

.main-menu {
  position: absolute;
  left: -200px;
  top: 0;
  height: 100%;
  overflow-x: visible;
  overflow-y: hidden;
  transition: left 0.3s ease, box-shadow 0.3s ease;
  z-index: 999;
}

.main-menu ul {
  list-style: none;
  margin: 0;
  padding: 2.5em 0 0;
  -webkit-box-shadow: -8px 0 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: -8px 0 8px rgba(0, 0, 0, 0.5);
  box-shadow: -8px 0 8px rgba(0, 0, 0, 0.5);
  min-height: 100%;
  width: 200px;
  background: #1a1a1a;
  font-size: 1.6rem;
}

.main-menu a {
  display: block;
  padding: .75em 15px;
  line-height: 1em;
  font-size: 1em;
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid #383838;
}

.main-menu a:hover,
.main-menu a:focus {
  background: #333;
  text-decoration: underline;
}

.main-menu:target {
  left: 0;
  outline: none;
  -moz-box-shadow: 3px 0 12px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 3px 0 12px rgba(0, 0, 0, 0.25);
  box-shadow: 3px 0 12px rgba(0, 0, 0, 0.25);
}

.main-menu:target .menu-close {
  z-index: 1001;
}

.main-menu:target ul {
  position: relative;
  z-index: 1000;
}

.main-menu:target + .backdrop {
  position: absolute;
  display: block;
  content: '';
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: #000;
  background: rgba(0, 0, 0, 0.85);
  cursor: default;
}

.main-menu li:first-child a {
  border-top: 1px solid #383838;
}

.main-menu .menu-close {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 1.6rem;
  border: none;
}

@supports (position: fixed) {
  .main-menu,
  .main-menu:target + .backdrop {
    position: fixed;
  }
}

@media screen and (min-width: 768px) {
  .menu-toggle {
    visibility: hidden;
    padding: 0;
  }

  header {
    background-color: transparent;
    position: initial;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  header .logo {
    position: relative;
  }

  /* Undo positioning of off-canvas menu */

  .main-menu {
    position: relative;
    left: auto;
    top: auto;
    height: auto;
  }

  .main-menu ul {
    display: flex;
    /* Undo off-canvas styling */
    padding: 0;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    height: auto;
    width: auto;
    background: none;
  }

  .main-menu a {
    border: 0 !important;
    /* Remove borders from off-canvas styling */
  }

  .main-menu a:hover {
    background-color: transparent;
    text-decoration: none;
  }

  .main-menu:hover,
  .main-menu:focus {
    background: none;
  }

  .main-menu .menu-close {
    display: none;
  }
}

